
import { defineComponent } from 'vue';
import {
  IonPage,
  IonRow,
  IonButton,
  IonIcon,
  IonButtons,
  IonSearchbar,
} from '@ionic/vue';
import Header from '@/components/ui/Header.vue';
import Product from '@/components/Product.vue';
import FilterModal from '@/components/FilterModal.vue';
import FilterElement from '@/components/FilterElement.vue';
import { mapActions, mapGetters, mapMutations } from 'vuex';
import Content from '@/components/ui/Content.vue';
import Loading from '@/components/ui/Loading.vue';

export default defineComponent({
  name: 'Catalog',
  components: {
    Loading,
    Content,
    FilterElement,
    FilterModal,
    Product,
    Header,
    IonPage,
    IonRow,
    IonIcon,
    IonButton,
    IonButtons,
    IonSearchbar,
  },
  data() {
    return {
      isFilter: false,
    };
  },
  computed: {
    ...mapGetters(['filter', 'products', 'loading', 'categories']),
  },
  async mounted() {
    this.SET_FILTER({
      type: [],
      period: [],
      sphere: {},
      radius: {},
      add: {},
      ax: {},
      cylinder: {},
      dominant: {},
    });
  },
  methods: {
    ...mapMutations([
      'SET_FILTER',
      'SET_META_PRODUCTS',
      'SET_TYPE',
      'SET_RADIUS',
      'SET_RADIUS',
      'SET_RADIUS',
      'SET_LOADING',
    ]),
    ...mapActions([
      'getProducts',
      'getTypes',
      'getRadius',
      'getSphere',
      'getAdds',
      'getAxes',
      'getCylinders',
      'getDominants',
      'getPeriod',
      'filterProducts',
      'getCategories',
    ]),
    // eslint-disable-next-line @typescript-eslint/no-empty-function
    async refresh(complete = () => {}) {
      try {
        await this.getCategories();
        await this.getPeriod();
        await this.getSphere();
        await this.getRadius();
        await this.getTypes();
        await this.getAdds();
        await this.getAxes();
        await this.getCylinders();
        await this.getDominants();
      } finally {
        complete();
      }
    },
    hide() {
      this.isFilter = false;
    },
    close(el: any) {
      this.SET_LOADING(true);
      const a = { ...this.filter };

      if (
        el.parent === 'sphere' ||
        el.parent === 'radius' ||
        el.parent === 'add' ||
        el.parent === 'ax' ||
        el.parent === 'cylinder' ||
        el.parent === 'dominant'
      ) {
        for (let key in a[el.parent]) {
          delete a[el.parent][key];
        }
        //Отправляем на фильтрацию
        this.SET_FILTER(a);
        this.filterProducts();
        this.SET_LOADING(false);
      }

      if (el.parent === 'type' || el.parent === 'period') {
        a[el.parent] = a[el.parent].filter((e: any) => e.id !== el.value.id);
        //Отправляем на фильтрацию
        this.SET_FILTER(a);
        this.filterProducts();
        this.SET_LOADING(false);
      }
    },
  },
});
